import React, { useState } from 'react';
import { CDBBtnGrp, CDBBtn, CDBContainer } from "cdbreact";
import { CDBSidebar, CDBSidebarHeader, CDBSidebarMenuItem, CDBSidebarContent, CDBSidebarMenu, CDBSidebarFooter } from 'cdbreact';
import { Container, Navbar, Nav } from "react-bootstrap";
import { Link, useMatch, useResolvedPath } from "react-router-dom";
import { useTranslation } from 'react-i18next';

function Item({ text, icon, to, closeNavbar }) {
    const resolvedPath = useResolvedPath(to);
    const isActive = useMatch({ path: resolvedPath.pathname, end: true });

    const handleClick = () => {
        closeNavbar();
    };

    return (
        <Nav.Link as={Link} to={to} className={`${isActive ? 'active' : ''} py-1 text-light`} onClick={handleClick}>
            <CDBSidebarMenuItem className='py-1' icon={icon} iconSize="lg">
                {text}
            </CDBSidebarMenuItem>
        </Nav.Link>
    );
}

const Sidebar = ({ items }) => {
    const { i18n } = useTranslation();
    const handleChangeLanguage = (lang) => {
        return () => {
            i18n.changeLanguage(lang);
            closeNavbar(); // Close navbar after changing language
        };
    };
    const [isSidebarOpen, setIsSidebarOpen] = useState(true);
    const [isNavbarOpen, setIsNavbarOpen] = useState(false);

    const toggleSidebar = () => {
        setIsSidebarOpen(!isSidebarOpen);
    };

    const toggleNavbar = () => {
        setIsNavbarOpen(!isNavbarOpen);
    };

    const closeNavbar = () => {
        setIsNavbarOpen(false);
    };

    const menuText = items.find(item => item.menu)?.menu || "";
    const websText = items.find(item => item.webs)?.webs || "";

    return (
        <>
            <div className="d-none d-lg-block">
                <CDBSidebar className={`position-fixed text-light bg-accent bg-blur ${isSidebarOpen ? 'sidebar-open' : 'sidebar-closed'}`}>
                    <CDBSidebarHeader prefix={<i className="fa fa-bars" />} onClick={toggleSidebar}>
                        {menuText}
                    </CDBSidebarHeader>
                    <CDBSidebarContent>
                        <CDBSidebarMenu>
                            {items.filter(item => !item.menu && !item.webs).map((item, index) => (
                                <Item key={index} text={item.text} icon={item.icon} to={item.to} closeNavbar={() => {}} />
                            ))}
                        </CDBSidebarMenu>
                    </CDBSidebarContent>
                    {isSidebarOpen && (
                        <>
                            <CDBContainer>
                                <CDBBtnGrp className='d-flex justify-content-between px-4'>
                                    <CDBBtn onClick={handleChangeLanguage("it")} className='bg-transparent text-decoration-none text-light'>IT</CDBBtn>
                                    <CDBBtn onClick={handleChangeLanguage("en")} className='bg-transparent text-decoration-none text-light'>EN</CDBBtn>
                                    <CDBBtn onClick={handleChangeLanguage("es")} className='bg-transparent text-decoration-none text-light'>ES</CDBBtn>
                                    <CDBBtn onClick={handleChangeLanguage("fr")} className='bg-transparent text-decoration-none text-light'>FR</CDBBtn>
                                </CDBBtnGrp>
                            </CDBContainer>
                            <CDBSidebarFooter className='text-center'>
                                <div className="sidebar-btn-wrapper px-2 py-3 text-center">
                                    <hr className='mb-4'></hr>
                                    <p className="fw-bold">{websText}</p>
                                    <p className='mb-2'>Matteo Migliarese</p>
                                </div>
                            </CDBSidebarFooter>
                        </>
                    )}
                </CDBSidebar>
            </div>

            <div className="d-lg-none">
                <Navbar expand="lg" className="bg-accent-mobile py-3 fixed-top w-100">
                    <Container>
                        <Navbar.Brand className='text-light d-flex justify-content-center align-items-center' href="#home">
                            Silvia Nives Vincitorio 
                        </Navbar.Brand>
                        <Navbar.Toggle aria-controls="basic-navbar-nav" onClick={toggleNavbar} style={{ color: '#fff' }}><i className="fa fa-bars" /></Navbar.Toggle>
                        <Navbar.Collapse id="basic-navbar-nav" in={isNavbarOpen}>
                            <Nav className="text-light">
                                {items.filter(item => !item.menu && !item.webs).map((item, index) => (
                                    <Item key={index} text={item.text} icon={item.icon} to={item.to} closeNavbar={closeNavbar} />
                                ))}
                                <hr></hr>
                                <CDBContainer>
                                    <CDBBtnGrp className='d-flex justify-content-between px-4'>
                                        <CDBBtn onClick={() => { handleChangeLanguage("it")(); closeNavbar(); }} className='bg-transparent text-decoration-none text-light'>IT</CDBBtn>
                                        <CDBBtn onClick={() => { handleChangeLanguage("en")(); closeNavbar(); }} className='bg-transparent text-decoration-none text-light'>EN</CDBBtn>
                                        <CDBBtn onClick={() => { handleChangeLanguage("es")(); closeNavbar(); }} className='bg-transparent text-decoration-none text-light'>ES</CDBBtn>
                                        <CDBBtn onClick={() => { handleChangeLanguage("fr")(); closeNavbar(); }} className='bg-transparent text-decoration-none text-light'>FR</CDBBtn>
                                    </CDBBtnGrp>
                                </CDBContainer>
                            </Nav>
                        </Navbar.Collapse>
                    </Container>
                </Navbar>
            </div>
        </>
    );
};

export default Sidebar;
